import {preview} from '../../common/js/common.js'
import {addressData} from '../../common/network/map.js'
import {mapState} from 'vuex'

export default{
	name:'Upload',
	data(){
		return{
			currentIndex:0,
			classify:['景点','美食','网红','打卡'],
			uploadedImg:[],//上传的图片
			videoSrc:'',//上传的视频
			book:'',
			address:'',
			classifyName:'',//分类名
			uploadTitle:'',//标题
			uploadContent:'',//内容
		}
	},
	computed:{
		...mapState(['city']),
		getCity(){
			this.address = this.city.city || '获取定位'
		}
	},
	methods:{
		// 点击分类
		tapItem(index,name){
			this.currentIndex = index
			this.classifyName = name
		},
		// 点击上传图片
		uploadImg(){
			uni.chooseImage({
				count:8,
				sizeType:['original','compressed'],
				sourceType:['album'],
				success:res => {
					console.log(res)
					this.uploadedImg.push(...res.tempFilePaths)
				}
			})
		},
		// 删除图片
		delImage(index){
			this.uploadedImg.splice(index,1)
		},
		// 预览图片
		previewImg(index){
			preview(index,this.uploadedImg)
			.then(res => {
				console.log('预览成功')
			})
			.catch(err => {
				console.log('预览失败')
			})
		},
		// 上传视频
		uploadVideo(){
			uni.showLoading({
				title:'上传中'
			})
			uni.chooseVideo({
				count:1,
				sourceType:['camera','album'],
				maxDuration:60,
				success:res => {
					this.videoSrc = res.tempFilePath
					this.book = res.thumbTempFilePath
					console.log(res)
					uni.hideLoading()
					uni.showToast({
						title:'上传成功',
						duration:1000,
						icon:'success'
					})
				},
				fail: err =>{
					console.log(err)
					uni.hideLoading()
					uni.showToast({
						title:'上传失败',
						duration:1000,
						icon:'none'
					})
				}
			})
		},
		// 删除视频
		delVideo(){
			this.videoSrc = ''
		},
		// 获取定位
		goAddress(){
			uni.navigateTo({
				url:'/pages/search/search'
			})
		},
		// 提示弹框
		toast(text){
			uni.showToast({
				title:text,
				icon:'none'
			})
		},
		// 发布
		isSubmit(){
			this.classifyName == '' ? this.classifyName = this.classify[0] : ''
			console.log(this.classifyName)
			console.log(this.uploadTitle)
			console.log(this.uploadContent)
			console.log(this.uploadedImg)
			console.log(this.videoSrc)
			console.log(this.address)
			if(this.uploadTitle == ''){
				this.toast('请输入标题')
			}else if(this.uploadContent == ''){
				this.toast('请输入发表内容')
			}else if(this.uploadedImg.length < 3){
				this.toast('上传的图片不小于三张')
			}else{
				this.toast('上传成功！')
				this.isLogin()
			}
		},
		// 判断是否登录
		isLogin(){
			// 请求数据库查看是否存在
			var db = wx.cloud.database()
			var user = db.collection('user')
			user.get().then(res => {
				console.log(res)
				if(res.data.length == 0){
					uni.showModal({
						content:'请登录',
						showCancel:true,
						cancelColor:'#f10c00',
						confirmText:'登录',
						success(res) {
							console.log(res)
						},
						fail(err) {
							console.log(err)
						}
					})
				}else{
					this.toast('已经登录')
				}
			}).catch(err => {
				this.toast('登录失败')
			})
		}
	}
}
